body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-size: cover;
  background-image: url('https://www.maxpixel.net/static/photo/1x/Soccer-Sport-Game-Goal-Competition-Field-Football-731432.jpg')

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  }

.player-list {
  text-align: center;
  display : flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: auto
}

.flag {
  width:130px;
  height: 70px;
  margin: 20px auto;
}

.photo {
  width:300px;
  height: 200px;
  margin: 20px auto;
}

.player {
  width : 340px;
  height : auto;
  border : 5px solid black;
  margin: 20px auto;
  background-color: black;
  color: lightgrey;
  padding:10px
}

h1, h2 {
  color : white;
  text-shadow: 2px 5px 5px black
}

input{
  margin: 20px 5px;
}

bouton {
  border: 1px solid white;
  padding: 3px
}
